// React
import React, { useState } from 'react';

// CSS
import './css/Calendar.css';

// Calendar
const Calendar = () => {

  // Previous
  const [pStart] = useState(29);
  const [pEnd] = useState(31);
  const pNumbers = Array.from({ length: pEnd - pStart + 1 }, (_, i) => i + pStart);

  // Next
  const [nStart] = useState(1);
  const [nEnd] = useState(2);
  const nNumbers = Array.from({ length: nEnd - nStart + 1 }, (_, i) => i + nStart);

  // Present
  const [start] = useState(1);
  const [end] = useState(30);
  const numbers = Array.from({ length: end - start + 1 }, (_, i) => i + start);

  // Component
  return (
    <div className="calendar">
      <div className="day">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
      <div className="date">

        {/* Previous */}
        {pNumbers.map((pNumber) => (
          <span key={pNumber} className="prev">{pNumber}</span>
        ))}

        {/* Present */}
        {numbers.map((number) => (
          <span key={number} className={`pres -s${number}`}>{number}</span>
        ))}

        {/* Next */}
        {nNumbers.map((nNumber) => (
          <span key={nNumber} className="next">{nNumber}</span>
        ))}
      </div>
    </div>
  );
}

export default Calendar;