// React
import React, { useState, useEffect } from 'react';
import { ReactLenis, useLenis } from '@studio-freight/react-lenis'
import FsLightbox from "fslightbox-react";

// Component
import Calendar from './component/Calendar';
import Cell from './component/Cell';
import FAB from './component/FAB';
import Footer from './component/Footer';
import Gallery from './component/Gallery';
import Loading from './component/Loading';
import MetaTheme from './component/MetaTheme';
import Section from './component/Section';
import SectionCover from './component/SectionCover';
import SectionHero from './component/SectionHero';
import Text from './component/Text';

// CSS
import './css/App.css';

// PlaceHolder
import ph11 from './source/ph-11.png';
import ph43 from './source/ph-43.png';
import ph169 from './source/ph-169.png';
import ph219 from './source/ph-219.png';

const gallery = [
  { src: ph11, alt: 'Title' },
  { src: ph43, alt: 'Title' },
  { src: ph169, alt: 'Title' },
  { src: ph219, alt: 'Title' },
  { src: ph11, alt: 'Title' },
  { src: ph43, alt: 'Title' },
  { src: ph169, alt: 'Title' },
  { src: ph219, alt: 'Title' },
  { src: ph11, alt: 'Title' },
  { src: ph43, alt: 'Title' },
  { src: ph169, alt: 'Title' },
  { src: ph219, alt: 'Title' },
];

// App
const App = () => {

  // Lightbox
  const handleClickImg = (event) => {
    const imageSrc = event.target.src;
    setLightboxSource([imageSrc]);
    setLightboxToggler(!lightboxToggler);
  };

  // Lightbox - Gallery
  const [lightboxSource, setLightboxSource] = useState([]);
  const [lightboxToggler, setLightboxToggler] = useState(false);
  const handleClickGallery = (index) => {
    const sortedSources = gallery.slice(index).concat(gallery.slice(0, index)).map(image => image.src);
    setLightboxSource(sortedSources);
    setLightboxToggler(!lightboxToggler);
  };

  // Lenis Stop
  const lenis = useLenis(() => { })
  useEffect(() => {
    const observer = new MutationObserver((mutations) => {
      mutations.forEach((mutation) => {
        if (mutation.type === "attributes" && mutation.attributeName === "class") {
          const hasFsLightboxOpen = document.documentElement.classList.contains('fslightbox-open');
          if (hasFsLightboxOpen) {
            lenis.stop();
          } else {
            lenis.start();
          }
        }
      });
    });
    observer.observe(document.documentElement, {
      attributes: true,
      attributeFilter: ['class']
    });
    return () => observer.disconnect();
  }, [lenis]);

  // Component
  return (
    <ReactLenis root>

      {/* MetaTheme */}
      <MetaTheme />

      {/* Lightbox */}
      <FsLightbox
        key={lightboxSource.length === 1 ? 'singleImage' : 'multipleImages'}
        toggler={lightboxToggler}
        sources={lightboxSource}
        slide={1}
      />

      {/* Loading */}
      <Loading />

      {/* FAB */}
      <FAB />

      {/* Hero */}
      <SectionHero />

      {/* Cover */}
      <SectionCover />

      {/* Info */}
      <Section id="date">
        <Text config="-label -tc"><strong>Schedule</strong></Text>
        <Cell config="-ac -gs">
          <Text config="-subheadline">3 11 ()  3</Text>
          <Text type="a" href="https://naver.me/GvkXFE6S"> </Text>
        </Cell>
        <Calendar />
      </Section>

      {/* Gallery */}
      <Section id="gallery">
        <h1><strong>Gallery</strong></h1>
        <Text config="-label -tc"><strong>Our Story</strong></Text>
        <Gallery images={gallery} onClick={handleClickGallery} />
      </Section>

      {/* Map */}
      <Section id="map">
        <h1><strong>Information</strong></h1>
        <Text config="-label -tc"><strong>Location</strong></Text>
        <Cell config="-ac -gs">
          <Text config="-subheadline"> </Text>
          <Text type="a" href="https://naver.me/GvkXFE6S">  126 23</Text>
        </Cell>
        <img src={ph169} alt="Title" onClick={handleClickImg} style={{ cursor: 'zoom-in' }} />
        <Cell>
          <Cell config="-mb">
            <Text config="-subtext"></Text>
          </Cell>
          <Cell config="-li">
            <Text config="-label">   2</Text>
            <Text>   2 ,  30 ,  500</Text>
          </Cell>
          <Cell config="-li">
            <Text config="-label"> </Text>
            <Text>    2 </Text>
          </Cell>
        </Cell>
        <Cell>
          <Cell config="-mb">
            <Text config="-subtext"></Text>
          </Cell>
          <Cell config="-li">
            <Text config="-label -ln2" bullet={true}> 2</Text>
            <Text>2   5 (500m)</Text>
          </Cell>
          <Cell config="-li">
            <Text config="-label -lnb" bullet={true}> </Text>
            <Text>1   5 (500m)</Text>
          </Cell>
          <Cell config="-li">
            <Text config="-label -lng" bullet={true}> </Text>
            <Text>8820, 2234, 21-1</Text>
          </Cell>
          <Cell config="-li">
            <Text config="-label -lng" bullet={true}> </Text>
            <Text>8820, 2234, 21-1</Text>
          </Cell>
        </Cell>
      </Section>

      {/* Contact */}
      <Section id="contact">
        <Text config="-label -tc"><strong>Contact</strong></Text>

        {/* Groom */}
        <Cell>
          <Cell config="-mb">
            <Text config="-subtext"></Text>
          </Cell>
          <Cell config="-li">
            <Text config="-label"></Text>
            <Cell>
              <Cell config="-rw">
                <Text config="-subtext"></Text>
                <Text>010-4245-8923</Text>
              </Cell>
              <Cell config="-rw">
                <Text config="-subtext"></Text>
                <Text> 110-223-9938492</Text>
              </Cell>
            </Cell>
          </Cell>
          <Cell config="-li">
            <Text config="-label"> ()</Text>
            <Cell config="-rw">
              <Text config="-subtext"></Text>
              <Text>010-4245-8923</Text>
            </Cell>
          </Cell>
          <Cell config="-li">
            <Text config="-label"> ()</Text>
            <Cell config="-rw">
              <Text config="-subtext"></Text>
              <Text>010-4245-8923</Text>
            </Cell>
          </Cell>
        </Cell>

        {/* Bride */}
        <Cell>
          <Cell config="-mb">
            <Text config="-subtext"></Text>
          </Cell>
          <Cell config="-li">
            <Text config="-label"></Text>
            <Cell>
              <Cell config="-rw">
                <Text config="-subtext"></Text>
                <Text>010-3498-0122</Text>
              </Cell>
              <Cell config="-rw">
                <Text config="-subtext"></Text>
                <Text> 110-223-9938492</Text>
              </Cell>
            </Cell>
          </Cell>
          <Cell config="-li">
            <Text config="-label"> ()</Text>
            <Cell config="-rw">
              <Text config="-subtext"></Text>
              <Text>010-4245-8923</Text>
            </Cell>
          </Cell>
          <Cell config="-li">
            <Text config="-label"> ()</Text>
            <Cell config="-rw">
              <Text config="-subtext"></Text>
              <Text>010-4245-8923</Text>
            </Cell>
          </Cell>
        </Cell>
      </Section>

      {/* Footer */}
      <Footer />

    </ReactLenis>
  );
}

export default App;