// React
import React, { useState, useEffect } from 'react';
import Masonry from "react-responsive-masonry";

// CSS
import './css/Gallery.css';

// Gallery
const Gallery = ({ images, onClick }) => {

  // Column Counts
  const [columnsCount, setColumnsCount] = useState(2);
  useEffect(() => {
    const updateColumnsCount = () => {
      const width = window.innerWidth;
      let columns = 2;
      if (images.length >= 3) {
        if (width > 1440) {
          columns = 3;
        } else {
          columns = 2;
        }
      }
      setColumnsCount(columns);
    };
    window.addEventListener("resize", updateColumnsCount);
    updateColumnsCount();
    return () => window.removeEventListener("resize", updateColumnsCount);
  }, [images.length]);

  // Handle Click
  const handleImageClick = (index) => {
    if (onClick) {
      onClick(index);
    }
  };

  //Component
  return (
    <div className="gallery">
      <Masonry columnsCount={columnsCount} gutter=".5rem">
        {images.map((image, index) => (
          <picture key={index} style={{ cursor: onClick ? 'zoom-in' : 'default' }} onClick={() => handleImageClick(index)}>
            <img src={image.src} alt={image.alt} />
          </picture>
        ))}
      </Masonry>
    </div>
  );
}

export default Gallery;