// React
import React, { useState, useEffect } from 'react';
import { useLenis } from '@studio-freight/react-lenis';

// CSS
import './css/FAB.css';

// FAB
const FAB = () => {

  // FAB Vision
  const [isVisible, setIsVisible] = useState(false);
  useEffect(() => {
    const checkScroll = () => {
      const coverElement = document.getElementById('info');
      if (coverElement) {
        const coverPosition = coverElement.offsetTop;
        const viewportHeight = window.innerHeight;
        const currentPosition = window.pageYOffset + viewportHeight;
        setIsVisible(currentPosition >= coverPosition);
      }
    };
    window.addEventListener('scroll', checkScroll);
    checkScroll();
    return () => {
      window.removeEventListener('scroll', checkScroll);
    };
  }, []);

  // Lenis Scroll To
  const lenis = useLenis();
  const date = () => lenis.scrollTo('#date');
  const gallery = () => lenis.scrollTo('#gallery');
  const map = () => lenis.scrollTo('#map');
  const contact = () => lenis.scrollTo('#contact');

  // Component
  return (
    <div className={`fab ${isVisible ? '-visible' : ''}`}>
      <span style={{ cursor: 'pointer' }} onClick={date}></span>
      <span style={{ cursor: 'pointer' }} onClick={gallery}></span>
      <span style={{ cursor: 'pointer' }} onClick={map}></span>
      <span style={{ cursor: 'pointer' }} onClick={contact}></span>
    </div>
  );
};

export default FAB;