// React
import React from 'react';
import { motion, useScroll, useTransform } from 'framer-motion';

// Component
import Cell from './Cell';

// CSS
import './css/SectionHero.css';

// Img
import img01 from './source/placeholder-01.jpg';
import img02 from './source/placeholder-02.jpg';
import img03 from './source/placeholder-03.jpg';
import img04 from './source/placeholder-04.jpg';
import img05 from './source/placeholder-05.jpg';

// Framer Transition
const transition = {
  type: "spring",
  damping: 25,
  stiffness: 120
};

// SectionHero
const SectionHero = () => {

  // Parallax
  const { scrollYProgress } = useScroll();
  const yRange = useTransform(scrollYProgress, [0, 0.75], ['0%', '-100%']);
  const sRange = useTransform(scrollYProgress, [0, 0.5], ['54rem', '27rem']);
  const oRange = useTransform(scrollYProgress, [0, 0.125], ['1', '0']);

  // Infinite Slide
  const slideInVariants = {
    hidden: { x: '0%' }, visible: {
      x: '-100%', transition: { duration: 24, ease: "linear", repeat: Infinity }
    }
  };

  // Component
  return (
    <motion.section id="hero" initial={{ y: "100vh" }} animate={{ y: 0 }} transition={{ ...transition, delay: 1.5 }}>

      {/* Title */}
      <Cell>
        <Cell config="-ac -hidn">
          <motion.h1 initial={{ y: "100%" }} animate={{ y: 0 }} transition={{ ...transition, delay: 1.6 }}><strong>Shawn</strong>EJ</motion.h1>
        </Cell>
        <Cell config="-ac -hidn">
          <motion.p className="text -label" initial={{ y: "100%" }} animate={{ y: 0 }} transition={{ ...transition, delay: 1.7 }}><strong>2125. 3. 1</strong></motion.p>
        </Cell>
      </Cell>

      {/* Invitation */}
      <Cell config="-ac -hidn">
        <motion.h1 initial={{ y: "100%" }} animate={{ y: 0 }} transition={{ ...transition, delay: 1.8 }}><strong>Wedding</strong></motion.h1>
      </Cell>

      {/* Image */}
      <motion.ul style={{ y: yRange, opacity: oRange }}>
        <motion.li variants={slideInVariants} initial="hidden" animate="visible" style={{ height: sRange }}>
          <div><img src={img01} alt="" /></div>
          <div><img src={img02} alt="" /></div>
          <div><img src={img03} alt="" /></div>
          <div><img src={img04} alt="" /></div>
          <div><img src={img05} alt="" /></div>
        </motion.li>
        <motion.li variants={slideInVariants} initial="hidden" animate="visible" style={{ height: sRange }}>
          <div><img src={img01} alt="" /></div>
          <div><img src={img02} alt="" /></div>
          <div><img src={img03} alt="" /></div>
          <div><img src={img04} alt="" /></div>
          <div><img src={img05} alt="" /></div>
        </motion.li>
        <motion.li variants={slideInVariants} initial="hidden" animate="visible" style={{ height: sRange }}>
          <div><img src={img01} alt="" /></div>
          <div><img src={img02} alt="" /></div>
          <div><img src={img03} alt="" /></div>
          <div><img src={img04} alt="" /></div>
          <div><img src={img05} alt="" /></div>
        </motion.li>
      </motion.ul>
    </motion.section>
  );
}

export default SectionHero;